<template>
  <div>
    <div>
      <div class="options-wrapper">
        <div v-for="option in optionsWithColor" :key="option.id">
          <button :class="optionClass(option.color)" @click="submitSingleAnswer(option.id)">
            {{ option.label }}
          </button>
        </div>
      </div>
    
      <div class="chart-wrapper">
        <img :src="chartUrl"/>
      </div>
    </div>
    <div class="total-line">
      Total number of votes recorded: {{ pollState.total }}
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "SingleAnswer",
  props: ["pid", "options"],
  data() {
    return {
      chartUrl: "http://chart.apis.google.com/chart?cht=p&chs=210x210"
    };
  },
  beforeMount() {
    this.updateChartUrl();
  },
  beforeUpdate() {
    this.updateChartUrl();
  },
  computed: {
    ...mapState({
      pollState: state => state.local
    }),
    optionsWithColor() {
      const colors = ["7babc5","e36d36","d4e4bc","96acb7","f9dbbd","ccf5ac","bad29f","808a9f"];
      return this.options.map(x => {
        x["color"] = colors.shift();
        return x;
      });
    }
  },
  methods: {
    optionClass(color) {
      return "option-button option-color-" + color;
    },
    updateChartUrl() {
      let vec = this.optionsWithColor.map(x => {
        let pid = x["id"];
        x["votes"] = this.pollState.result[pid] || 0;
        return x;
      });

      let color = vec.map(x => x.color).join("|");
      let data = vec.map(x => x.votes).join(",");
      let chp = vec[1].votes / (vec[0].votes + vec[1].votes) * 3.6;
      this.chartUrl = `http://chart.apis.google.com/chart?cht=p&chs=210x210&chp=${chp}&chd=t0:${data}&chco=${color}&chf=a,s,000000|bg,s,e6e6e6`;
    },
    submitSingleAnswer(answer) {
      console.log(
        `submitted single answer for pid ${this.pid} with answer ${answer}`
      );
      this.$store.commit("local/singleInc", answer);
      this.updateChartUrl();
      this.$forceUpdate();
    }
  }
};
</script>

<style scoped>
.options-wrapper {
  width: 40%;
  float: left;
}
.option-button {
  min-width: 5em;
  padding: 8px;
  margin-bottom: 10px;
  font-size: 120%;
  color: white;
  text-transform: uppercase; 
  border: 0;
  background: none;
  cursor: pointer;
  outline: none;
}
.option-color-7babc5 {
  background-color: #7babc5;
}
.option-color-e36d36 {
  background-color: #e36d36;
}
.chart-wrapper {
  padding-top: 20px;
  float: left;
}
.total-line {
  padding-top: 20px;
  font-family: sans-serif;
  font-size: 90%;
  clear: left;
}
</style>



