<template>
  <div>
    <div class="options-wrapper">
      <div class="option-item" v-for="option in optionsWithColor" :key="option.id">
        <div class="input-checkbox">
          <input :id="option.id" type="checkbox" :value="option.id" v-model="answers" /> 
          <label :class="labelClass(option.color)" :for="option.id"></label>
        </div>
        <div class="input-label">
          {{ option.label }}
        </div>
      </div>

      <button class="submit-button" @click="submitMultiAnswer">Submit</button>

    </div>

    <div class="chart-wrapper">
      <img :src="chartUrl"/>
    </div>

    <div class="total-line">
      Total number of votes recorded: {{ pollState.total }}
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'MultipleAnswer',
  props: ['pid', 'options'],
  beforeMount() {
    this.updateChartUrl();
  },
  beforeUpdate() {
    this.updateChartUrl();
  },
  data() {
    return {
      answers: [],
      chartUrl: "http://chart.apis.google.com/chart?cht=p&chs=210x210"
    }
  },
  computed: {
    ...mapState({
      pollState: state => state.local
    }),
    optionsWithColor() {
      const colors = ["7babc5","e36d36","d4e4bc","96acb7","f9dbbd","ccf5ac","bad29f","808a9f"];
      return this.options.map(x => {
        x["color"] = colors.shift();
        return x;
      });
    }
  },
  methods: {
    labelClass(color) {
      return "label-color-" + color;
    },
    updateChartUrl() {
      let vec = this.optionsWithColor.map(x => {
        let pid = x["id"];
        x["votes"] = this.pollState.result[pid] || 0;
        return x;
      });

      let color = vec.map(x => x.color).join("|");
      let data = vec.map(x => x.votes).join(",");
      let chp = vec[1].votes / (vec[0].votes + vec[1].votes) * 3.6;
      this.chartUrl = `http://chart.apis.google.com/chart?cht=p&chs=210x210&chp=${chp}&chd=t0:${data}&chco=${color}&chf=a,s,000000|bg,s,e6e6e6`;
    },
    submitMultiAnswer() {
      console.log(`submitted multi answer for pid ${this.pid} with answers ${this.answers}`);
      this.$store.commit('local/multiInc', this.answers);
      this.$forceUpdate();
    }
  }
}
</script>

<style scoped>
.label-color-7babc5 { background-color: #7babc5; }
.label-color-e36d36 { background-color: #e36d36; }
.label-color-d4e4bc { background-color: #d4e4bc; }
.label-color-96acb7 { background-color: #96acb7; }
.label-color-f9dbbd { background-color: #f9dbbd; }
.label-color-ccf5ac { background-color: #ccf5ac; }

input[type=checkbox] {
	visibility: hidden;
}
.option-item {
  clear: left;
  padding: 5px;
}
.input-checkbox {
  position: relative;
  display: inline;
}
.input-label {
  display: inline;
  margin-left: 30px;
  font-family: sans-serif;
  font-size: 130%;
  white-space: nowrap;
}
.input-checkbox label {
	cursor: pointer;
	position: absolute;
	width: 20px;
	height: 20px;
	top: 0;
	border-radius: 4px;
}
.input-checkbox label:after {
	filter: alpha(opacity=0);
	opacity: 0;
	content: '';
	position: absolute;
	width: 9px;
	height: 5px;
	background: transparent;
	top: 4px;
	left: 4px;
	border: 3px solid #333;
	border-top: none;
	border-right: none;

	-webkit-transform: rotate(-45deg);
	-moz-transform: rotate(-45deg);
	-o-transform: rotate(-45deg);
	-ms-transform: rotate(-45deg);
	transform: rotate(-45deg);
}
.input-checkbox input[type=checkbox]:checked + label:after {
	filter: alpha(opacity=100);
	opacity: 1;
}

.submit-button {
  display: block;
  padding: 8px;
  margin-top: 20px;
  font-size: 90%;
  color: white;
  text-transform: uppercase; 
  border: none;
  background-color: #163c6a;
  cursor: pointer;
  outline: none;
}
.options-wrapper {
  width: 50%;
  float: left;
}
.chart-wrapper {
  padding-top: 20px;
  float: left;
}
.total-line {
  padding-top: 20px;
  font-family: sans-serif;
  font-size: 90%;
  clear: left;
}
@media screen and (max-width: 650px) {
  .options-wrapper {
    width: 100%;
    float: none;
  }
}
</style>



