<template>
  <div>
    <div class="active-poll-wrapper">
      <ActivePoll :poll="getActivePoll()"></ActivePoll>
    </div>
    <div class="poll-list-wrapper">
      <div v-for="poll in getRestPollList()" :key="poll.id" class="poll-list-item">
        <span class="poll-list-icon"><img src="./assets/poll.png" /></span>
        <span class="poll-list-date">{{ poll.publishedDate | moment("DD MMM YYYY") }}</span>
        <span class="poll-list-title" @click="viewPoll(poll.id)">
          {{ poll.title }}
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import jsonData from './json/poll.json'
import store from './store'
import ActivePoll from './components/ActivePoll.vue'

export default {
  name: 'app',
  store,
  data () {
    return {
      pollData: jsonData.polls,
      activePollId: 0
    }
  },
  beforeMount() {
    this.activePollId = this.pollData[0].id;
  },
  components: {
    ActivePoll
  },
  methods: {
    viewPoll(id) {
      const currentPollId = this.activePollId;
      this.$store.dispatch('local/pushPollState', currentPollId);
      
      this.activePollId = id;
      this.$store.dispatch('local/fetchPollState', this.activePollId);
    },
    getRestPollList() {
      return this.pollData.filter(p => p.id !== this.activePollId);
    },
    getActivePoll() {
      return this.pollData.find(p => p.id === this.activePollId);
    }
  }
}
</script>

<style scoped>
.active-poll-wrapper {
  background: #e6e6e6;
  padding: 40px;
}
.poll-list-wrapper {
  margin-top: 20px;
}
.poll-list-item {
  width: 40%;
  padding: 20px;
  list-style: none;
  float: left;
  border-bottom: 1px solid gray;
}
.poll-list-title {
  cursor: pointer;
}
.poll-list-icon {
  float: left;
  margin-right: 10px;
}
.poll-list-item:nth-child(odd) {
  clear: left;
  border-right: 1px solid gray;
}
.poll-list-item:nth-last-child(-n+2) {
  border-bottom: none;
}
.poll-list-date {
  text-transform: uppercase;
  font-size: 70%;
  font-family: sans-serif;
  font-weight: bold;
  color: #163c6a;
  display: block;
  margin-bottom: 5px;
}
@media screen and (max-width: 650px) {
  .poll-list-item {
    width: 80%;
    border-right: none; 
    float: none;
    margin-bottom: 10px;
    min-height: 60px;
  }
  .poll-list-item:nth-child(odd) {
    border-right: none;
  }
  .poll-list-item:nth-last-child(-n+2) {
    border-bottom: 1px solid gray;
  }
  .poll-list-item:last-child {
    border-bottom: none;
  }
  .poll-list-icon {
    padding-bottom: 50px;
  }
}
</style>



