<template>
  <div>
    <div>
      <span class="poll-title">{{ poll.title }}</span>
      <span class="poll-date active-poll-date">{{ poll.publishedDate | moment("DD MMM YYYY") }}</span>
    </div>
  
    <div class="answer-wrapper">
      <SingleAnswer v-if="poll.answer.type === 'Single'" :pid="poll.id" :options="poll.answer.options"></SingleAnswer>
      <MultipleAnswer v-else-if="poll.answer.type === 'Multi'" :pid="poll.id" :options="poll.answer.options"></MultipleAnswer>
    </div>
  </div>
</template>

<script>
import SingleAnswer from './SingleAnswer.vue'
import MultipleAnswer from './MultipleAnswer.vue'

export default {
  name: 'ActivePoll',
  props: ['poll'],
  beforeMount() {
    this.$store.dispatch('local/fetchPollState', this.poll.id);
  },
  beforeUpdate() {
    this.$store.dispatch('local/fetchPollState', this.poll.id);
  },
  beforeDestroy() {
    this.$store.dispatch('local/pushPollState', this.poll.id);
  },
  components: {
    SingleAnswer,
    MultipleAnswer
  }
}
</script>

<style scoped>
.poll-title {
  font-size: 130%;
}
.poll-date {
  text-transform: uppercase;
  font-size: 70%;
  font-family: sans-serif;
  font-weight: bold;
  color: #163c6a;
  margin-left: 15px;
}
.answer-wrapper {
  padding: 20px;
}
@media screen and (max-width: 650px) {
  .poll-date {
    display:block;
    margin-top: 10px;
    margin-left: 0;
  }
}
</style>



