import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    global: {
      namespaced: true,
      state: {},
      getters: {
        stateByPid: state => pid => {
          return state[pid] || {};
        }
      },
      mutations: {
        updatePoll(state, { pid, data }) {
          console.log(`update global state with pid ${pid} and data ${JSON.stringify(data)}`);
          state[pid] = data;
        }
      }
    },
    local: {
      namespaced: true,
      state: {
        pid: 0,
        result: {},
        total: 0
      },
      mutations: {
        singleInc(state, answer) {
          state.result[answer] = state.result[answer] || 0;
          state.result[answer]++;
          state.total++;
        },
        multiInc(state, answers) {
          answers.forEach(x => {
            state.result[x] = state.result[x] || 0;
            state.result[x]++;
            state.total++;
          });
        },
        resetPollState(state, { pid, data }) {
          console.log(`reset local poll state for pid ${pid} to ${JSON.stringify(data)}`)
          state.pid = pid;
          state.result = data;
          state.total = Object.values(data).reduce((acc, v) => acc + v, 0);
        }
      },
      actions: {
        fetchPollState(context, pid) {
          new Promise(resolve => {
            resolve(context.rootGetters['global/stateByPid'](pid));
          }).then(data => {
            console.log(`fetch poll from global for pid ${pid} with data ${JSON.stringify(data)}`);
            context.commit('resetPollState', { "pid": pid, "data": data });
          })
        },
        pushPollState(context, pid) {
          if (pid !== context.state.pid) {
            console.error("inconsistent pid found when pushing poll state");
          } else {
            console.log(`push poll to global pid ${pid} with data ${JSON.stringify(context.state.result)}`);
            context.commit('global/updatePoll', { "pid": pid, "data": context.state.result }, { root: true });
          }
        }
      }
    } 
  }
});


